import Vue from 'vue'
import Router from 'vue-router'

import layout from '@/components/layout/main' // 
import main from '@/components/main' // 

// 
import login from '@/components/account/login/main'

// 
import loginlog from '@/components/modules/log/loginlog/main' // 
import optlog from '@/components/modules/log/optlog/main' // 

// 
import opt from '@/components/modules/sys/opt/main' // 
import user from '@/components/modules/sys/user/main' // 

//
import button from '@/components/modules/component/button/main'//
import upload from '@/components/modules/component/upload/main'//
import editor from '@/components/modules/component/editor/main'//

//
import notFound from '@/components/error/notFound' //
import serverError from '@/components/error/serverError' //

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/login',
    component: login
  }, {
    path: '/serverError',
    component: serverError
  }, {
    path: '/',
    component: login,
    name: '',
    children: []
  }, {
    path: '/index',
    component: layout,
    name: '',
    children: [
      { path: '/index', component: main }
    ]
  }, {
    path: '/log',
    component: layout,
    name: '',
    perm_code: 'log',
    children: [
      { path: '/log/optlog', component: optlog, perm_code: 'log:optlog', name: '' },
      { path: '/log/loginlog', component: loginlog, perm_code: 'log:loginlog', name: '' }
    ]
  }, {
    path: '/sys',
    component: layout,
    name: '',
    perm_code: 'sys',
    children: [
      { path: '/sys/user', component: user, perm_code: 'sys:user', name: '' },
      { path: '/sys/opt', component: opt, perm_code: 'sys:opt', name: '' },
    ]
  }, {
    path: '/component',
    component: layout,
    name: '',
    perm_code: 'component',
    children: [
      { path: '/component/button', component: button, perm_code: 'component:button', name: '' },
      { path: '/component/upload', component: upload, perm_code: 'component:upload', name: '' },
      { path: '/component/editor', component: editor, perm_code: 'component:editor', name: '' }
    ]
  }, {
    path: '*',
    component: notFound
  }]
})


