import layer from '@/assets/js/layer'
import ajax from '@/assets/js/ajax'

let request = {
  //
  addRecord: (url, data, success, fail) => {
    return ajax.request({
      type: 'post',
      url: url,
      data: data,
      success: success,
      fail: fail
    })
  },

  // 
  deleteRecord: (url, id, success, fail) => {
    let requestUrl = url + id
    return ajax.request({
      type: 'delete',
      url: requestUrl,
      success: success,
      fail: fail
    })
  },

  // 
  deleteList: (url, id) => {},

  //  ()
  getList: (url, data, success, fail, noLogin) => {
    let requestUrl = url
    return ajax.request({
      type: 'post',
      url: requestUrl,
      data: data,
      success: success,
      fail: fail,
      noLogin: noLogin
    })
  },
  // 
  getDetail: (url, id, success, fail) => {
    let requestUrl = url + id
    return ajax.request({
      type: 'get',
      url: requestUrl,
      success: success,
      fail: fail
    })
  },

  // 
  updateRecord: (url, id, data, success, fail) => {
    let requestUrl = url + id
    let sendData = data
    sendData['_method'] = 'put'

    return ajax.request({
      type: 'post',
      url: requestUrl,
      data: sendData,
      success: success,
      fail: fail
    })
  },

  //  
  getDict: (type, success, fail) => {
    const url = '/sys/basicdata/dict/' + type

    return this.a.getList(url, {}, success, fail)
  },

  //  
  submit: (url, data, success, fail) => {
    return ajax.request({
      type: 'post',
      url: url,
      data: data,
      success: success,
      fail: fail
    })
  },

  // post 
  post: (url, data, success, fail) => {
    return ajax.request({
      type: 'post',
      url: url,
      data: data,
      success: success,
      fail: fail
    })
  },

  // get 
  get: (url, data, success, fail) => {
    return ajax.request({
      type: 'get',
      url: url,
      data: data,
      success: success,
      fail: fail
    })
  },

}

export default request


