export default (res) => {
  const records = res.data.records
  let listPrems = {}
  const ext = res.ext

  // records
  if (records && records.length > 0) {
    for (let key in records[0]) {
      listPrems[key] = true
    }
  }

  // 
  if (ext && ext.length > 0) {
    for (let i = 0; i < ext.length; i++) {
      listPrems[ext[i]] = false
    }
  }

  return listPrems
}


